#search {
    padding: 0.2rem;
    width: 25rem;
}

#search-and-button {
    margin: 0 auto;
}

input {
    border: none;
    border-radius: 20px;
    background-color: #80ced6;
    margin: 0 auto;
    height: 60px;
    font-size: 20px;
    margin-right: 20px;
}

button {
    height: 60px;
    padding: 20px;
    cursor: pointer;
    border: none;
    border-radius: 20px;
    background-color: #80ced6;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}

button:hover {
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2);
}

.shadow-5 {
    -webkit-box-shadow: 4px 4px 8px 0px rgba( 0, 0, 0, 0.2);
            box-shadow: 4px 4px 8px 0px rgba( 0, 0, 0, 0.2);
}


